    }

    &:hover {
        background: darken($tmpcolor, 1);
        border-color: $tmpcolor;
    }

    &.btn-with-rightside-icon {
        span {
            &:before {
                position: absolute;
                content: "";
                right: 45px;
                top: 23px;
                background-image: url(../img/signin-arrow.png);
                background-size: contain;
                background-repeat: no-repeat;
                height: 24px;
                width: 27px;
            }
        }
    }
}